import React from "react";
import Box from '@mui/material/Box';
import { 
    ListSubheader, 
    List, 
    ListItem, 
    ListItemIcon, 
    ListItemText,
    Collapse,
    ListItemButton,
    // ExpandLess,
    // ExpandMore 
} from '@mui/material';
import { ThemeProvider } from "@emotion/react";
import ChatIcon from '@mui/icons-material/Chat';
import IconButton from '@mui/material/IconButton';
// import List from "@material-ui/core/List";
// import ListItem from "@material-ui/core/ListItem";
// import ListItemIcon from "@material-ui/core/ListItemIcon";
// import ListItemText from "@material-ui/core/ListItemText";
// import Collapse from "@material-ui/core/Collapse";
// import ExpandLess from "@material-ui/icons/ExpandLess";
// import ExpandMore from "@material-ui/icons/ExpandMore";


class ContactsList extends React.Component {
    state = { open: {} };

    handleClick = key => () => {
        // console.log(key);
        this.setState({ [key]: !this.state[key] });
    };

    handleMessage = email => () => {
        // console.log(email)
        this.props.sendMessage(email)
    }
    
    render() {
        const { lists, classes } = this.props;
    
        return (
            <ThemeProvider theme={this.props.theme}>
          <Box sx={{ 
              width: '100%',
              color:'text.primary',
              maxWidth: 480
          }} >
            <List
              component="nav"
              subheader={
                <ListSubheader >Contacts</ListSubheader>
              }
            >
              {lists.map(({ key, label, icon: Icon, items }) => {
                const open = this.state[key] || false;
                return (
                  <div key={key}>
                    <ListItem button onClick={this.handleClick(key)}>
                      {/* <ListItemIcon>
                        <Icon />
                      </ListItemIcon> */}
                      <ListItemText inset primary={label} />
                      {/* {open ? <ExpandLess /> : <ExpandMore />} */}
                    </ListItem>
                    <Collapse in={open} timeout="auto" unmountOnExit>
                      <List component="div" disablePadding>
                        {items.map(({ key: childKey, label: childLabel, email: childEmail}) => (
                            <Box>
                            
                          <ListItem key={childKey} button>
                          <ListItemButton>
                        {/* //   className={classes.nested} */}
                          
                            {/* <ListItemIcon> */}
                              {/* <ChildIcon /> */}
                            {/* </ListItemIcon> */}
                                <ListItemText inset primary={childLabel} />
                                </ListItemButton>
                            </ListItem>
                            <ListItem>
                                <ListItemButton>
                                    <ListItemText inset primary={childEmail} />
                                    <IconButton color="inherit" edge="end" aria-label="chat"  onClick={this.handleMessage(childEmail)}>
                                        <ChatIcon />
                                    </IconButton>
                                </ListItemButton>
                          </ListItem>
                          </Box>
                        ))}
                      </List>
                    </Collapse>
                  </div>
                );
              })}
            </List>
        </Box>
        </ThemeProvider>
        );
      }
    
}

export default ContactsList;