class App extends Component {
    constructor(props) {
        super(props);
        const lightTheme = createTheme({
            palette: {
                mode: 'light',
            },
        });
        
    
        this.state = {theme : lightTheme}
        microsoftTeams.app.initialize();    
        microsoftTeams.app.registerOnThemeChangeHandler(this.updateTheme.bind(this));

        microsoftTeams.app.getContext().then((context) => {
            this.updateTheme(context.app.theme)
        });
    }

    sendMessage =(email) => {
        console.log(email)
        const usr = [];
        usr.push(email)
        if(microsoftTeams.chat.isSupported()) {
            const chatPromise = microsoftTeams.chat.openGroupChat({ users: usr, topic: "", message: ""});
            chatPromise.
              then((result) => {console.log(result)}).
              catch((error) => {console.log(error)});
        }
        else { console.log('chat not supported') }
    }

    updateTheme = (theme) => {
        if(theme === 'default') {
            console.log('def')
            const lightTheme = createTheme({
                palette: {
                    mode: 'light',
                },
            });
            this.setState({theme : lightTheme})
        } else {
            console.log('dark')
            const darkTheme = createTheme({
                palette: {
                    mode: 'dark',
                },
            });
            this.setState({theme : darkTheme})
            console.log(this.state.theme)
        }
    }

    render() {
        return (
            <div className="App">
                <Box
                    sx={{
                        display: 'flex',
                        width: '100%',
                        alignItems: 'center',
                        justifyContent: 'center',
                        backgroundColor: 'theme.palette.background.paper',
                        color: 'text.primary',
                        borderRadius: 1,
                        p: 3,
                    }}
                >
                    <ThemeProvider theme={this.state.theme}>
                        {/* <Button color="primary">Primary</Button>
                        <Button color="secondary">Secondary</Button>
                        <Typography color='text.primary'> 
                            Hello, React! 
                        </Typography> */}
                        <ContactsList lists= {lists} theme={this.state.theme} sendMessage ={this.sendMessage} ></ContactsList>
                    </ThemeProvider>
                </Box>
          </div>
        )
    }
}

export default App